.support-banner {
	position:relative;
	// margin:0 0 50px;
	@include screen(custom, min, $min-md) {
		padding:0 15px;
	}
	@include screen(custom, max, $max-sm) {
		margin:20px -15px 0;
	}
	.visual {
		position: relative;
		width:100%;
		height:400px;
		overflow: hidden;
		img {
			position: absolute;
			top:0;
			left:50%;
			transform:translateX(-50%);
		}
		@include screen(custom, max, $max-sm) {
			height:260px;
		}
	}
	.text-block {
		position:absolute;
		top:50%; left:50%;
		transform: translate(-50%,-50%);
		max-width:1284px;
		margin:auto;
		width:100%;
		@include screen(custom, max, $max-sm) {
			position:static;
			height:auto;
			width:100%;
			transform: none;
			padding:0 24px;
		}
		.vertical-wrap {
			padding:0 109px*100%/1284px;
			@include screen(custom, max, $max-sm) {
				padding:20px 0;
			}
		}
		.name {
			margin:0;
			color:$color-black;
			@include font-family($font-semibold);
			font-size:40px;
			line-height:48px;
			@include screen(custom, max, $max-lg) {
				font-size:34px;
				line-height:40px;
			}
			@include screen(custom, max, $max-md) {
				font-size:28px;
				line-height:36px;
			}
			@include screen(custom, max, $max-sm) {
				font-size:24px;
				line-height:36px;
			}
		}
		.info {
			// flexible declaration
			// max-width:50%;

			// fixed declaration
			max-width:280px;
			margin-top: 6px;
			@include font-family($font-regular);
			font-size:16px;
			line-height:24px;
			color:$color-nightrider;
			@include screen(custom, max, $max-sm) {
				max-width:none;
				font-size:14px;
				line-height:20px;
				margin-top: 4px;
			}
		}
		.btn-area {
			margin-top:24px;
			@include screen(custom, max, $max-sm) {
				margin-top:19px;
				.btn {width:100%;}
			}
		}
		&.white {
			.name,
			.info {color:$color-white;}
			@include screen(custom, max, $max-sm){
				.name,
				.info {color:$color-black;}
			}
		}
	}
	// margin variation
	// & + .contact-us-link-menu, 
	// & + .feedback-area {
	// 	padding-top:0;
	// } 
}

.support-banner-review {
	position:relative;
	overflow:hidden;
	height:180px;
	margin:8px 0 32px;
	@include screen(custom, max, $max-sm){
		height:auto;
	}
	.visual {
		position:relative;
		height:100%;
		img {
			position:absolute;
			left:50%;
			top:0;
			transform:translateX(-50%);
			height:100%;
			@include screen(custom, max, $max-sm){
				position:static;
				width:100%;
				transform:translateX(0);
			}
		}
	}
	.text-block {
		position:absolute;
		left:0;
		top:50%;
		width:100%;
		transform:translateY(-50%);
		@include screen(custom, max, $max-sm){
			top:0;
			transform:translateY(0);
		}
		.text-area {
			padding-left:55.6%;
			@include screen(custom, max, $max-sm){
				padding:26px 30px 0;
			}
		}
		.tit {
			display:block;
			@include font-family($font-semibold);
			font-size:24px;
			color:#333;
			line-height:24px;
			@include screen(custom, max, $max-sm){
				font-size:20px;
			}
		}
		.desc {
			margin:6px 0 0 0;
			font-size:16px;
			color:#333;
		}
		.btns {
			margin-top:12px;
		}
	}
}