// 200511  -  RTL
.support-product-area {
	position:relative;
	max-width:1314px;
	margin:0 auto;
	padding:0 15px;
	@include screen(custom, max, $max-sm) {
		padding:0;
	}
	.inner {
		padding:0 0 20px;
		border-bottom:1px solid $line-gray;
		@extend %clearfix;
		@include screen(custom, max, $max-sm) {
			margin:0 9px;
			padding:40px 0;
		}
	}
	.visual {
		float:left;
		width:25%;
		&-image {
			width:87%;
			img {
				width:100%;
			}
		}
		@include screen(custom, max, $max-sm) {
			float:none;
			width:auto;
			text-align:center;
			&-image {
				width:100%;
				max-width:210px;
				margin:0 auto;
			}
		}
		[dir=rtl] & {
			float:right;
			@include screen(custom, max, $max-sm) {
				float:none;
			}
		}
	}
	.text-block {
		float:left;
		width:75%;
		padding:17px 0 0;
		// @include screen(custom, max, $max-lg) {padding-left:70px;}
		// @include screen(custom, max, $max-md) {padding-left:40px;}
		@include screen(custom, max, $max-sm) {
			float:none;
			width:auto;
			margin-top:21px;
			padding:0;
			text-align:center;
		}
		[dir=rtl] & {
			float:right;
			@include screen(custom, max, $max-sm) {
				float:none;
			}
		}
		.model {
			@include font-family($font-semibold);
			font-size:40px;
			color:$color-black;
			// @include screen(custom, max, $max-lg) {
			// 	padding-top:18px;
			// 	font-size:34px;
			// 	line-height:40px;
			// }
			// @include screen(custom, max, $max-md) {
			// 	padding-top:15px;
			// 	font-size:28px;
			// 	line-height:30px;
			// }
			// @include screen(custom, max, $max-sm) {
			// 	padding-top:22px;
			// }
			@include screen(custom, max, $max-sm) {
				font-size:28px;
			}
		}
		.name {
			margin:4px 0 0;
			@include font-family($font-semibold);
			font-size:16px;
			line-height:24px;
			color:$color-dimgray;
			@include screen(custom, max, $max-sm) {
				margin-top:7px;
			}
		}
		.support-wrap {
			margin-top:27px;
			@include screen(custom, max, $max-md) {
				margin-top:23px;
				border:1px solid $line-gray;			
				&.active {
					.support-title .btn-support:after {
						background-image: url(/lg5-common-gp/images/common/icons/arrow-up-black.svg);
					}
				}
			}
			.support-title {
				margin-bottom:20px;
				@include screen(custom, max, $max-md) {
					margin-bottom:0;
				}
				// 200311 START 
				.btn-support {
					padding:0;
					@include font-family($font-bold);
					font-size:16px;
					color:$color-nightrider;
					text-align:left;
					border:none;
					background:none;
					@include screen(custom, max, $max-md) {
						display:block;
						position:relative;
						width:100%;
						padding:14px 20px;
						@include font-family($font-semibold);
						font-size:14px;
						background:$bg-whitesmoke;
						&:after {
							content: '';
							position: absolute;
							right: 17px;
							top: 50%;
							width: 20px;
							height: 20px;
							-webkit-transform: translateY(-50%);
							-ms-transform: translateY(-50%);
							transform: translateY(-50%);
							background: url(/lg5-common-gp/images/common/icons/arrow-down-black.svg) no-repeat;
						}
						&[aria-expanded=true]:after {
							background-image:url(/lg5-common-gp/images/common/icons/arrow-up-black.svg);
						}
					}
					[dir=rtl] & {
						text-align:right;
						&:after {
							right:auto;
							left:17px;
						}
					}
				}
				&-text {
					padding:0;
					@include font-family($font-bold);
					font-size:16px;
					color:$color-nightrider;
					border:none;
					background:none;
					@include screen(custom, max, $max-md) {
						display:none;
					}
				}
				// 200311 END 
			}
			.support-list {
				display:flex;
				@include screen(custom, max, $max-md) {
					display:none;
					padding:16px 20px;
					border-top:1px solid $line-gray;
				}
				.item {
					width:20%;
					font-size:0;
					text-align:center;
					border-right:1px solid $line-gray;
					&:first-child {
						border-left:1px solid $line-gray;
					}
					@include screen(custom, max, $max-md) {
						float:none;
						width:100% !important;
						text-align:left;
						border:0 none;
						&:first-child {
							border:0 none;
						}
						& + .item {
							margin-top:13px;
						}
					}
					[dir=rtl] & {
						&:first-child {
							border-left:0 none;
						}
						&:last-child {
							border-left:1px solid $line-gray;
						}
						@include screen(custom, max, $max-md) {
							&:first-child, &:last-child {
								border:0 none;
							}
						}
					}
					// 200311 START 
					a {
						display:block;
						height:100%;
						padding:0 6px;
						text-align:center;
						background:none;
						&:hover .link-text {
							text-decoration:underline;
						}
						@include screen(custom, max, $max-md) {
							padding:0;
							text-align:left;
						}
						[dir=rtl] & {
							@include screen(custom, max, $max-md) {
								text-align:right;
							}
						}
					}
					// 200311 END 
				}
				.icon {
					display:block;
					width:48px;
					height:48px;
					margin:0 auto 12px; 
					img {
						width:100%;
						height:100%;
					}
					@include screen(custom, max, $max-md) {
						display:none;
					}
				}
				.link-text {
					display:block;
					margin:0 auto;
					color:$color-nightrider;
					line-height:16px;
					word-break:keep-all;
					word-break:break-word;
					&:after {
						background-image:url('/lg5-common-gp/images/common/icons/link-right-nightrider.svg');
					}
				}
			}
		}
		// 20200427 START 
		.link-area{
			padding: 27px 0 0;
			@include screen(custom, max, $max-sm) {
				padding: 17px 0 0;
			}
			.link-text{
				margin-right: 13px;
				@include screen(custom, max, $max-sm) {
					display: block;
					margin-right: 0;
				}
				& + .link-text{
					@include screen(custom, max, $max-sm) {
						margin-top:9px;
					}
				}
			}
		}
		.btn-area{
			margin: 34px 0 0;
			@include screen(custom, max, $max-sm) {
				margin: 25px 0 0;
			}
			.btn{
				margin-right: 6px;
				margin-bottom: 10px;
				@include screen(custom, max, $max-sm) {
					display: block;
					margin-right: 0;
					margin-bottom: 0;
					margin-top:10px;
				}
			}
		}
		// 20200427 END 
	}
}