.category-browse-area {
	position: relative;
	$button-rounding:3px;
	.category-content {
		overflow:hidden;
		max-height:0;
		transition:max-height 300ms ease;
		//.solutions-contents,
		.btn-close {
			display:none;
		}
		&:focus {
			outline:none;
		}
		&.active {
			height:auto; max-height:760px;
			transition:max-height 300ms ease;
			.solutions-contents, 
			.btn-close {
				display:block;
			}
		}
		@include screen(custom, max, $max-sm) {
			margin:0 -15px;
			&.active {
				margin-top: -12px;
			}
		}
	}
	// area original custom :: cascade solution-contents
	.solutions-contents {
		margin-top: -3px;
		display:none;
		.title {
			text-align: left;
			margin:0 -15px;
			padding:0;
		}
		.title .head {
			display:none;
		}
		.title .desc{
			@include font-family($font-semibold);
			font-size:24px;
			line-height:32px;
			color:$color-nightrider;
		}
		.product-type {
			margin-bottom:20px;
		}
		.product-type .item {
			padding-top:38px;
		}
		@include screen(custom, max, $max-sm) {
			font-size:20px;
		}
		@include screen(custom, max, $max-sm){
			.title {
				text-align: center;
			}
		}
	}
	// category name area
	.category-name-area{
		position: relative;
		border-bottom:2px solid $color-nightrider;
		padding-bottom: 19px;
		margin-bottom: 35px;
		
		.category-name {
			@include font-family($font-semibold);
			font-size:40px;
			line-height: 48px;
			color:$color-black;
		}
		.btn-open {
			position: absolute;
			right:0; top:30px;
			font-size:16px;
			line-height:20px;
			button {
				background-color:transparent;
				border:none;
				font-size:inherit;
				padding:0;
				&.ico-bottom {
					&:after {
						@extend %linkicon;margin-left:10px;
						background:url('/lg5-common-gp/images/common/icons/arrow-down-carmine.svg') no-repeat 0 2px;
					}
				}
			}
			
			[dir="rtl"] & {
				right:auto;
				left:0;
			}
		}

		
		.ico-right:before {
			width:calc(100% - 20px);
		}
		.ico-right:after {
			margin-left:10px;
			transform:rotate(90deg);
		}
		@include screen(custom, max, $max-sm){
			padding-bottom:10px;
			margin: 0 9px 24px;
			.category-name {
				font-size:28px;
				line-height:36px;
			}
			.btn-open {
				position:static;
				display:block;
				padding:11px 0 13px;
				background-color:$bg-whitesmoke;
				text-align: center;
				margin:-32px -24px 30px;
				border-bottom:1px solid $line-gray;
				font-size:14px;
			}
		}
	}
	.btn-close {
		@include screen(custom, min, $min-md){
			position: absolute;
			top:10px;right: 0;
			button {
				width:20px; height:20px;
				background: url(/lg5-common-gp/images/common/icons/modal-close.svg) no-repeat 0 0;
				background-size:20px 20px;
				padding: 0;
				border: 0;
				color:transparent;
			}
		}
		@include screen(custom, max, $max-sm){
			background-color:$bg-whitesmoke;
			text-align: center;
			margin:0 -24px 32px;
			border-top:1px solid $line-gray;
			border-bottom:1px solid $line-gray;
			padding: 10px 0 13px;
			button {
				position:relative;
				padding-right:17px;
				background:none;
				border:none;
				color:$color-carmine;
				&:before {
					display:block;
					width:calc(100% - 17px);
				}
				&:after {
					content:'';
					display:block;
					position:absolute;
					right:0px;
					top:7px;
					width:9px;
					height:9px;
					background:url(/lg5-common-gp/images/support/icons/more-close-carmine.svg) 0 0 no-repeat;
					background-size:100% 100%;
				}
			}
			button span {
				position: relative;
				display:inline-block;
				color:$color-carmine;
				@include font-family($font-regular);
				font-size:14px;
				line-height:1.5;
				text-decoration:none;
				padding-right:23px;
				&:before {
					@include pseudo();
					bottom:3px;left:0;width:100%;height:1px;
					background:$bg-carmine;
					width:calc(100% - 23px);
				}
				&:after {
					@include pseudo();
					right:0;top:8px;width:12px;height:12px;
					background:url('/lg5-common-gp/images/support/icons/link-bottom-carmine.svg') no-repeat 0 0;
				}
			}
		}
	}
}