.no-files-wrap {
	padding:25px 0;
	text-align:center;
	.icon {
		display:block;
		margin-bottom:7px;
	}
	.title {
		@include font-family($font-semibold);
		font-size:20px;
		line-height:40px;
		color:$color-black;
		margin:0 0 4px;
	}
	.desc {
		font-size:16px;
		line-height:24px;
		color:$color-dimgray;
		margin:0;
	}	
}

