.support-utility {
	position:relative;
	padding:21px 32px 25px;
	@include screen(custom, max, $max-sm) { 
		padding:16px 20px 25px;
		margin:20px 9px 0;
	}
	// @include screen(md) {
	// 	margin:0 15px;
	// }
	.title {
		@include font-family($font-semibold);
		font-size:24px;
		line-height:32px;
		margin:0 0 16px;
		@include screen(custom, max, $max-sm) {
			font-size:20px;
			line-height:28px;
		}
	}
	ul {
		@extend %listreset;
		li {
			margin-bottom:6px;
			&:last-child {
				margin-bottom:0;
			}
		}
	}
	a {
		color:$color-dimgray;
		&:before {
			background:$color-dimgray;
		}
	}
}
