// 200511  -  RTL
@import './support/no-content';

$mFcGap: -24px;// fluid container gap for mobile

.support-help-library {
	position:relative;
	@include screen(custom, max, $max-md) {
		margin-bottom:20px;
	}
	@include screen(custom, max, $max-sm) {
		margin:0 9px;
		&:after { // bottom gap
			content: '';
			display: block;
			width: auto;
			height: 20px;
			background: #f2f2f2;
			margin: 0 $mFcGap;
		}
	}
	// title style
	.support-header {
		overflow: hidden;
		position: relative;
		margin-bottom: 11px;
		margin-top:-5px;
		.title {
			font-size: 32px;
			line-height: 40px;
			@include font-family($font-semibold);
			margin: 0;
		}
		.btn-see-more {
			position: absolute;
			right: 0;
			top: 16px;
			[dir=rtl] & {
				right:auto;
				left:0;
			}
		}
		& + .support-body {
			border-top: 1px solid $line-gray; 
			padding-top: 20px;
		}

		@include screen(custom, max, $max-sm) {
			.title {
				font-size: 24px;
				line-height: 32px;
			}
			.btn-more {
				font-size: 14px;
			}
			& + .support-body {
				padding-top: 17px
			}
		}
	}

	.support-header .title .topic-name {
		font-size:16px;
		&:before {
			content:"";
			display:inline-block;
			vertical-align:middle;
			width:1px; height:15px;
			background-color:$color-nightrider;
			margin:-6px 14px 0 8px;
		}
	}
	
	// body contents layout
	.support-body {
		// overflow: hidden;
		display: table;
		table-layout: fixed;
		width: 100%;
		> .column {
			vertical-align: top;
			display: table-cell;
			padding-top: 20px;
		}
		@include screen(custom, max, $max-sm) {
			display: block;
			> .column {
				padding-top: 0;
				display: block;
			}
		}
	}
	// tab menu
	.tabs-menu-wrapper {
		width: 194px;
		border-right: 1px solid $line-gray;
		box-sizing: border-box;
		// scroll bar frame
		@include screen(custom, min, $min-md) {
			.menu-list-container {
				height:300px;
			}
			// 20200609 START  
			// .mCSB_inside>.mCSB_container {
			// 	margin-right:0;
			// }
			// 20200609 END 
		}
		.title {
			@include font-family($font-bold);
			font-size: 17px;
			line-height: 1;
			text-transform: uppercase;
			margin-bottom: 23px;
		}
		.menu {
			margin-top: 16px;
			a {
				color: $color-dimgray;
				@include font-family($font-semibold);
				font-size: 16px;
				line-height: 1;
				position: relative;
				display: inline-block;
				margin-right: 1em;
				text-decoration:none;
				[dir=rtl] & {
					margin-right:0;
					margin-left:1em;
				}
			}
			&.active {
				a {
					color: $color-carmine;
					margin-right: 0;
					padding-right:19px;
					&:after {
						@include pseudo();
						right:0;bottom:3px;width:12px;height:12px;
						background:url('/lg5-common-gp/images/common/icons/arrow-right-carmine.svg') no-repeat 0 0;
					}
					[dir=rtl] & {
						margin-left:0;
						padding-right:0;
						padding-left:19px;
						&:after {
							right:auto;
							left:0;
							transform:rotate(180deg);
						}
					}
				}
			}
		}
		@include screen(custom, max, $max-sm) {
			width: 100%;
			border-right: none;
			margin-bottom: 30px;
			.title {
				font-size: 14px;
				margin-bottom: 9px;
			}
			.form-control {
				font-size: 16px;
				color: $color-dimgray;
				border-radius: 0;
				border-color: #a4a4a4;
				padding: 11px 12px 10px 5px;
				height: auto;
			}
		}
		[dir=rtl] & {
			border-right:0 none;
			border-left:1px solid $line-gray;
			@include screen(custom, max, $max-sm) {
				border-left:0 none;
				.form-control {
					padding:11px 5px 10px 12px;
				}
			}
		}
	}
	.tabs-contents-wrapper {
		width: inherit;
		padding-left: 50px;
		@include screen(custom, max, $max-lg) {
			padding-left: 25px;
		}
		@include screen(custom, max, $max-md) {
			padding-left: 25px;
		}
		@include screen(custom, max, $max-sm) {
			padding-left: 0;
		}
		[dir=rtl] & {
			padding-left:0;
			padding-right:50px;
			@include screen(custom, max, $max-lg) {
				padding-right: 25px;
			}
			@include screen(custom, max, $max-md) {
				padding-right: 25px;
			}
			@include screen(custom, max, $max-sm) {
				padding-right: 0;
			}
		}
		.unit-list {
			margin-bottom: 35px;
		}
		.unit {
			margin-top: 33px;
			padding-top: 30px;
			border-top: 1px solid $line-lightgray;
			display: table;
			table-layout: fixed;
			width: 100%;
			@include screen(custom, max, $max-sm) {
				margin-top: 20px;
				padding-top: 20px;
			}
			&:first-child {
				margin-top: 0;
				padding-top: 20px;
				border-top: none;
			}
			.icon-wrapper,
			dl {
				display: table-cell;
				vertical-align: top;
			}
			.icon-wrapper {
				// width: 64px;
				width: 44px;
				@include screen(custom, min, $min-md) {
					padding-right: 20px;
					[dir=rtl] & {
						padding-right:0;
						padding-left:20px;
					}
				}
				box-sizing: content-box;
			}
			.icon {
				@include svgCarmineIcon();
				[dir=rtl] & {
					margin-right:0;
					margin-left:10px;
				}
			}
			

			.eyebrow {
				@include font-family($font-semibold);
				color: $color-dimgray;
				font-size: 14px;
				line-height: 1;
				padding-bottom:2px;
			}
			dt a {
				font-size: 24px;
				line-height: 32px;
				color: $color-nightrider;
				font-weight: normal;
				width: 100%;
				display: block;
				@include screen(custom, min, $min-md) {
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
				}
			}
			dd {
				margin:1em 0 0;
				font-size: 16px;
				line-height: 24px;
				color: $color-dimgray;
				overflow: hidden;
				text-overflow: ellipsis;
				display: block; 
				display: -webkit-box;
				-webkit-box-orient: vertical;
				// word-break:break-all;
				@include screen(custom, min, $min-md) {
					max-height: 72px;
					-webkit-line-clamp: 3; 
				}
				@include screen(custom, max, $max-sm) {
					max-height: 40px;
					-webkit-line-clamp: 2; 
				}
			}
			@include screen(custom, max, $max-sm) {
				.eyebrow {
					margin-top: -3px;
					margin-bottom: 2px;
				}
				dt a {
					font-size: 18px;
					line-height: 24px;
				}
				dd {
					font-size: 14px;
					line-height: 20px;
					margin-top: 9px;
				}
			}
		}
		.btn-see-more {
			position: absolute;
			right: 0;
			top: 16px;
			.link-text {
				display: inline-block!important;
			}
			@include screen(custom, max, $max-sm) {
				display:block;
				position:static;
				text-align: right;
				border-top: 1px solid $line-gray;
				text-decoration: underline;
				margin: 0 $mFcGap;
				padding: 14px 24px 14px;
			}
			[dir=rtl] & {
				right:auto;
				left:0;
				@include screen(custom, max, $max-sm) {
					text-align:left;
				}
			}
		}		
	}
	// nom side navigation type :: solutions
	&.no-snb-type {
		.tabs-contents-wrapper {
			padding-left: 0;
			[dir=rtl] & {
				padding-right:0;
			}
		}
		.support-header {
			margin-bottom: 0;
		}
		.support-header .title .topic-name {
			font-size:16px;
			&:before {
				content:"";
				display:inline-block;
				vertical-align:middle;
				width:1px; height:15px;
				background-color:$color-nightrider;
				margin:-6px 14px 0 8px;
			}
		}
		.tabs-exchange-list {
			@include font-family($font-bold);
			line-height:1;
			margin:12px 0 -1px;
			.btn-tab {
				font-size:14px;
			}
		}
		.no-content {
			margin:0;
			//padding-top:90px - 17px;
			padding-top:130px;
		}
	}
	.no-content {
		margin-left:-25px;
		@include screen(custom, max, $max-sm){
			margin-left:0;
			padding-top:90px - 30px;
		}
		[dir=rtl] & {
			margin-left:0;
			margin-right:-25px;
			@include screen(custom, max, $max-sm){
				margin-right:0;
			}
		}
	}
	.recommendation {
		margin-top: -5px;
		@include screen(custom, max, $max-sm){
			margin-top: -3px;
			margin-right:0;
			margin-left:8px;
		}
	}
}