.support-compliance {
	position:relative;
	padding:22px 32px;
	margin-bottom:20px;

	@include screen(custom, max, $max-sm) { 
		padding:16px 20px 25px;
		margin:20px 9px 0;
	}
	
	.area-title {
		@include font-family($font-semibold);
		font-size:24px;
		line-height:32px;
		margin:0 0 12px;
		color:$color-black;
		@include screen(custom, max, $max-sm) {
			font-size:20px;
			line-height:28px;
		}
	}
	ul {
		@extend %listreset;
		li {
			margin-bottom:6px;
			&:last-child {
				margin-bottom:0;
			}
		}
	}
}
