// 200511  -  RTL
.support-downloads {
	+ .support-downloads {
		@include screen(custom, max, $max-sm) {
			margin-top:-10px;
		}
	}
	position:relative;
	padding:22px 32px 30px;
	margin-bottom:20px;
	@include screen(custom, max, $max-sm) {
		padding:28px 20px 31px;
		margin:20px 9px;
		.no-files-wrap {
			display:none;
		}
	}
	.title-area {
		margin:0 0 17px;
		@include screen(custom, max, $max-sm) {
			margin-bottom:0;
		}
		.icon,
		.link-text:not(.ico-popup) {
			display:none;
		}
		@extend %clearfix;
		@include screen(custom, max, $max-sm) {	
			.icon {
				display:block;
				float:left;
				width:44px;
				height:44px;
				margin-right:20px;
				margin-top:5px;
				img { 
					width:100%;
				}
				[dir=rtl] & {
					float:right;
					margin-right:0;
					margin-left:20px;
				}
			}
			.link-text:not(.ico-popup) {
				display:inline-block;
			}
			.link-text.ico-popup {
				display:none;
			}
			.title-area-wrap {
				float:left;
				[dir=rtl] & {
					float:right;
				}
			}
			
		}
		.title {
			@include font-family($font-semibold);
			font-size:24px;
			line-height:32px;
			color:$color-black;
			@include screen(custom, max, $max-sm) {
				font-size:20px;
				line-height:28px;
				margin-bottom:0;
			}
			
		}
	}
	.list {
		@extend %listreset;
		@include screen(custom, max, $max-sm) {
			display:none;
		}
		> li {
			border-bottom:1px solid $line-lightgray;
			padding-left:30px;
			position:relative;
			margin-bottom:14px;
			padding-bottom:10px;
			@include screen(custom, max, $max-sm) {
				word-break:break-all;
			}
			[dir=rtl] & {
				padding-left:0;
				padding-right:30px;
			}
			&:last-child {
				border-bottom:0;
				margin-bottom:0;
				padding-bottom:0;
			}
			&:after {
				content:'';
				display:block;
				position:absolute;
				left:0;
				top:5px;
				width:20px;
				height:20px;
				[dir=rtl] & {
					left:auto;
					right:0;
				}
			}
			&.manuals {
				&:after {
					background: url('/lg5-common-gp/images/support/psp-download/documents.svg') 0 0 no-repeat; 
				}
			}
			&.software {
				&:after {
					background: url('/lg5-common-gp/images/support/psp-download/software-drivers.svg') 0 0 no-repeat; 
				}
			}
			.type {
				
				@include font-family($font-semibold);
				font-size:14px;
				line-height:20px;
				color:$color-dimgray;
				
			}
			.name {
				@include font-family($font-regular);
				font-size:16px;
				line-height:24px;
				color:$color-nightrider;
				//@include truncate(100%);
				.link-text {
					color:$color-nightrider;
					//&:before {
					//	width:calc(100%-18px);
					//	background-color:$bg-nightrider;
					//}
					&.ico-download:after {
						background-image:url('/lg5-common-gp/images/common/icons/download-nightrider.svg');
					}
					&.ico-blank:after {
						background-image:url('/lg5-common-gp/images/common/icons/blank-nightrider.svg');
					}
				}
			}
			.data {
				@include font-family($font-regular);
				font-size:14px;
				line-height:20px;
				color:$color-dimgray;
				margin-top:3px;
				span {
					display:inline-block;
					position:relative;
					&:first-child {
						margin-right:12px;
						padding-right:12px;
						&:after {
							content:'';
							display:block;
							position:absolute;
							top:0;
							right:0;
							width:1px;
							height:10px;
							top:50%;
							margin-top:-5px;
							background:#767676;
						}
					}
					&:only-child {
						margin:0;
						padding:0;
						&:after {
							display:none;
						}
					}
					[dir=rtl] & {
						&:first-child {
							margin-right:0;
							margin-left:12px;
							padding-right:0;
							padding-left:12px;
							&:after {
								right:auto;
								left:0;
							}
						}
						&:only-child {
							margin:0;
							padding:0;
							&:after {
								display:none;
							}
						}
					}
				}
			}
			
		}

		//Download
		&.download-list {
			> li {
				padding-left:0;
				.name {
					@include font-family($font-semibold);
				}
				.links {
					padding:2px 0 7px;
					@extend %clearfix;
					.empty {
						color:$color-gray;
					}
					li {
						position:relative;float:left;padding:0 24px 0 0;
						&:first-child {
							padding-left:0;
						}
						&:last-child{
							padding-right:0;
							&:before {
								display:none;
							}
						}
						&:before {
							@include pseudo();
							right:13px;top:7px;
							width:1px;height:9px;background:#999;
						}
					}
				}
			}
		}
	}
	/* LGERU-37 20200630 add */
	.software-static {
		position: relative;
		padding-left: 30px;
		box-sizing: border-box;
		&:after {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 5px;
			width: 20px;
			height: 20px;
			background: url('/lg5-common-gp/images/support/psp-download/software-drivers.svg') 0 0 no-repeat; 
			[dir=rtl] & {
				left: auto;
				right: 0;
			}
		}
		@include screen(custom, max, $max-sm) {
			word-break: break-all;
			padding-left: 0;
			&:after{
				display: none;
			}
		}
		.type {
			@include font-family($font-semibold);
			font-size: 14px;
			line-height: 20px;
			color: $color-dimgray;
		}
		.name {
			@include font-family($font-regular);
			font-size: 16px;
			line-height: 24px;
			color: $color-nightrider;
			.link-text {
				color: $color-nightrider;
				&.ico-download:after {
					background-image: url('/lg5-common-gp/images/common/icons/download-nightrider.svg');
				}
			}
		}
	}
	/* //LGERU-37 20200630 add */
	.btn-area {
		position:relative;
		text-align:center;
		margin:25px 0 0;
		@include screen(custom, max, $max-sm) {
			display:none;
		}
	}
	.with-mobile-case {
		max-width:300px;
		text-align: center;
		font-size:16px;
		line-height: 24px;
		color:$color-dimgray;
		padding:25px 0;
		margin:auto;
		@include screen(custom, max, $max-sm) {
			display:none;
		}
	}
	//Download
	.result {
		margin:10px 0 0;
		padding:4px 0 0;
		border-top:1px solid $line-lightgray;
		text-align:right;
		font-size:14px;
		line-height:20px;
		color:$color-dimgray;
		@include screen(custom, max, $max-sm) {display:none;}
	}
	// 20200319 START  ||  4   
	.pagination {
		padding:0 15px;
		margin-top:25px;
		max-width:270px;

		@include screen(custom, max, $max-sm) {display:none;}
		@include screen(custom, max, $max-lg) {
			max-width:211px;
			padding:0 15px;
			height:26px;			
		}
		.prev,
		.next {
			width:15px;			
			&.disable {display:none;}
			@include screen(custom, max, $max-lg) {
				width:15px;height:26px;
			}
		}

		.prev {
			background: url(/lg5-common-gp/images/common/icons/page-left.svg) no-repeat 0px 10px;
			@include screen(custom, max, $max-lg) {
				background-position:0px 6px;
			}
		}

		.next {
			background: url(/lg5-common-gp/images/common/icons/page-right.svg) no-repeat 0px 10px;
			@include screen(custom, max, $max-lg) {
				background-position:0px 6px;
			}
		}


		li {
			@include screen(custom, max, $max-lg) {
				margin:0px 1px;
			}
			button {
				min-width:36px;
				@include screen(custom, max, $max-lg) {					
					min-width:32px;height:26px;font-size:10px;					
				}
			}
		}
	}
	// 20200319 END  ||  4   
}

