.solutions-contents {
	position:relative;
	max-width:(1284px+30px);
	margin:0 auto;
	@include screen(custom, min, $min-md) {
		padding:0 30px;
	}
	@include screen(custom, max, $max-sm) {
		margin:0 9px 30px;
	}
	.title {
		text-align:center;
		padding:38px 0 0 0;
		@include screen(custom, max, $max-sm) {
			padding: 0;
		}
		strong, h1, h2, h3, h4, h5, h6 {
			@include font-family($font-semibold);
			font-size:40px;line-height:48px;color:$color-black;
			@include screen(custom, max, $max-sm) {
				font-size:28px;line-height:36px;
			}
		}
		.desc {
			@include font-family($font-regular);
			font-size:16px;
			padding:5px 0 24px 0;
			color: $color-dimgray;
			@include screen(custom, max, $max-sm) {
				font-size:14px;line-height:18px;
				padding-top: 0;
			}
		}
	}
	.product-type-gp {
		position:relative;
		width:100%;
		padding:0 108px;
		margin-bottom: 40px;
		// max-height:592px;
		overflow:hidden;
		@include screen(custom, max, (1284+30)) {
			padding:0 50px;
		}
		@include screen(custom, max, $max-sm) {
			padding: 0;
			margin-bottom:0;
		}

		.slick-track {
			display:flex;
			flex-wrap:wrap;
		}

		.items {
			@extend %clearfix;
			width:100%;
			.slick-list {
				width:auto;
				height:100%;
				overflow:hidden;
				@include screen(custom, max, $max-sm) {
					margin: 0 0 0 3px;
				}
			}
			.slick-arrow {
				padding:0;
				&.slick-prev,
				&.slick-next {
					position:absolute;
					top:50%;
					width:27px;
					height:46px;
					transform: translate(0,-50%);
				}
				&.slick-prev {
					left:58px;
				}
				&.slick-next {
					right:58px;
				}
				@include screen(custom, max, (1284+30)) {
					&.slick-prev {
						left:0;
					}
					&.slick-next {
						right:0;
					}
				}
			}
			opacity:0;
			&.slick-initialized {
				opacity:1;
			}

			&.lte-2 {
				.slick-arrow {
					display: none;
				}
				.slick-dots {
					visibility: hidden;
				}
			}
			@include screen(custom, min, $min-md) {
				&.lte-3 {
					.slick-arrow {
						display: none;
					}
					.slick-dots {
						visibility: hidden;
					}
				}
			}
			@include screen(custom, min, $min-lg) {
				&.lte-4 {
					.slick-arrow {
						display: none;
					}
					.slick-dots {
						visibility: hidden;
					}
				}
			}
		}
		.item {
			position:relative;
			float:left;
			padding:20px 0 0;
			outline: none;
			@include screen(custom, max, $max-sm) {
				padding-top: 10px;
			}
			&:before {
				@include pseudo();
				width:1px;height:100%;
				background:$line-lightgray;
				left:0;top:0;
			}
			// @include screen(custom, min, $min-lg) {
			// 	&:nth-child(4n-3) {&:before {display:none;}}
			// }
			// @include screen(md) {
			// 	&:nth-child(3n-2) {&:before {display:none;}}
			// }
			// @include screen(custom, max, $max-sm) {
			// 	&:nth-child(2n-1) {&:before {display:none;}}
			// }
			.visual,
			.category {
				text-align: center;
				margin:0 auto;
			}
			.visual {
				// background:rgba(165,0,52,0.2);
				// img {width:100%;height:auto;}
				// width: auto;
				max-width:160px;
				img {
					width: 100%;
				}
				@include screen(custom, min, $min-md) {
					
				}
				@include screen(custom, max, $max-sm) {
					max-width:150px;
					padding: 0 15px;
					box-sizing: border-box;
					img {
						max-width: 100%;
						// width: auto !important;
					}
                }
                
                a {
                    .image-box {
                        span {
                            position:relative;
                            display:block;
                            width:100%; padding-top:100%;
                        }
                        img {
                            position: absolute;
                            top:0;
                            bottom:0;
                            left:0;
                            right:0;
                            margin:auto;
                            max-width:100%; 
                            max-height:100%;
                        }
                    }
    
                    .text {
                        @include font-family($font-semibold);
                        display:inline-block;
                        width: 100%;
                        padding: 0px;
                        margin-top: 24px;
                        color: $color-black;
                        font-size: 16px;
                        line-height: 22px;
                        text-decoration: none;
                        @include screen(custom, max, $max-sm) {
                            font-size: 16px;
                        }
                    }
    
                }
            }
		}

		// slick carousel stylesheet
		.slick-slider {
			button {
				appearance: none;
				border: none;
				background-color: transparent;
				text-indent: -9999em;
				line-height: 0;
				// outline: none;
			}
			.slick-track {
				// necessary for item's bleet hidden 
				margin-left: -1px;
				margin-right: -1px;
			}
			.slick-arrow {
				svg {
					display: block;
				}
				color: $color-gray;
				&:hover,
				&:focus {
					color: $color-nightrider;
				}
			}
			.slick-dots {
				font-size: 0;
				text-align: center;
				margin: 37px 0 0;
				@include screen(custom, min, $min-md) {
					padding-bottom: 30px;
				}
				@include screen(custom, max, $max-sm) {
					margin: 30px 0 0;
				}
				li {
					display: inline-block;
					width: 12px;
					height: 12px;
					position: relative;
					border-radius: 50%;
					margin-left: 10px;
					line-height:normal;
					&:first-child {
						margin-left: 0;
					}
					@include screen(custom, max, $max-sm) {
						width:10px;
						height:10px;
						margin-left: 8px;
					}
					a, button {
						background: $color-gray;
						display:block;
						width:100%;
						height:100%;
						margin:0;
						padding:0;
						border:0;
						border-radius:50%;
						&:focus {
							outline:1px dotted;outline:5px auto -webkit-focus-ring-color;
						}
					}
					&.slick-active {
						a, button {
							background: $color-carmine;
						}
					}
					&:only-child {
						display:none;
					}
				}
			}
        }
        
        .search-depth-stack {
			position:relative;
			display:none;
			&.active {
				display:block;
			}
			@include screen(custom, max, $max-sm) {
				margin-bottom:19px;
			}
			.phase-reload {
				.prev-step {
					width:40px;height:40px;
					background-color:#f5f5f5;
					margin-right: 15px;
					border:none;
					overflow:hidden;
					vertical-align:middle;
					cursor: pointer;
					img {
						width:20px;
						height:20px;
					}
				}
				.name-of-phase {
					@include font-family($font-bold);
					font-size:16px;
					line-height:20px;
					color:$color-dimgray;
					text-transform: capitalize;
					vertical-align:middle;
				}
			}
			.change-filter {
				display:none;
				&.active {
					display:block;
				}
				padding:12px 0 0 0;
				select.run-chosen {
					width:100%;
				}
				@include screen(custom, min, $min-md) {
					position:absolute;
					right:0;top:0;
					width:160px;
					padding:0;
					.chosen-single {
						padding:9px 31px 9px 9px;
						font-size:14px;
						color:#767676;
					}
					.chosen-container-active {
						.chosen-single {
							padding:8px 30px 8px 8px;
						}
					}
					.chosen-container {
						font-size:14px;
					}
				}
			}
		}
	}
}
