@charset 'UTF-8';

@import './setting/variables';

// container
.support-wrap {
	aside &:nth-child(1) > div.support-downloads {
		margin-top:10px;
		@include screen(custom, max, $max-md) {
			margin-top:0;
		}
	}
	@import './support/solutions-contents';
	// GP Modify | select a product category -->
	@import './support/solutions-contents-gp';

	@import './support/support-search-area';
	@import './support/product-area';
	@import './support/download';
	@import './support/utility';
	@import './support/category-browse';
	@import './support/help-library';
	@import './support/no-files';
	@import './support/banner';
	@import './support/banner-full';
	@import './support/compliance-information';
	@import './modal/form-area';
	@import './modal/find-my-model';
}
.support-size {
	@extend %clearfix;
	max-width:(1284px+30px);
	padding:0;
	margin:0 auto;
	&.solutions {
		margin:40px auto 60px;
		padding:0 15px;
		@include screen(custom, max, $max-md) {
			margin-top:32px;
			margin-bottom:0;
		}
		@include screen(custom, max, $max-sm) {
			padding:0;
		}
	}
	// & + .support-banner,
	// & + .support-banner-full {
	// 	margin-bottom: 0;
	// }
}

.help-library-area {float:left;width:(872*100%/1284);}
.aside-area {
	float:right;width:(352*100%/1284);margin-top:5px;
	@include screen(custom, max, $max-sm) {
		margin-top:0;
	}
}

[dir="rtl"] {
	.help-library-area {float:right;}
	.aside-area {float:left;}
}

@include screen(lg) {
	.help-library-area {width:(760*100/1200);}
	.aside-area {width:(352*100%/1200);}
}
@include screen(custom, max, $max-md) {
	.help-library-area,
	.aside-area {
		float:none;
		width:100%;
	}
	.help-library-area .tabs-contents-wrapper .unit dd {
		height: auto;
	}
}

@include screen(custom, max, $max-md) {
	.support-wrap .support-banner-full {
		margin-bottom:20px
	}
}